<template>
	<dt-scroll-box class="proApplyCheck-page">
		<div slot="top">
			<div class="clearfix" style="marginBottom:20px">
				<p class="fl txt">项目申请审核</p>
				<div class="fr clearfix">
					<el-button class="dt-btn fl" @click="agree()">同意</el-button>
					<el-button class="dt-btn fl" @click="refuse()">拒绝</el-button>
				</div>
			</div>
		</div>
		<div slot="main-content" class="mainContent">
			<el-form>
				<div>
					<!-- 项目申请审核 界面 -->
					<h3 class="title">2019年医科院创新工程-人工智能项目申报审批单</h3>
					<!-- 上部分选择区 -->
					<div class="selection">
						<div class="clearfix">
							<!-- 左部分 -->
							<div class="form-left form-item fl">
								<el-form-item label="申报单号：">
									<p v-text='applyNum'></p>
								</el-form-item>
								<el-form-item label="申请单位：">
									<p v-text='applyYear'></p>
								</el-form-item>
							</div>

							<!-- 中间部分 -->
							<div class="form-middle form-item fl">
								<el-form-item label="申请人：">
									<p v-text='applyYear'></p>
								</el-form-item>
								<el-form-item label="项目状态：">
									<p v-text='applyYear'></p>
								</el-form-item>
								<!-- <el-form-item label="邮箱：">
                <el-input class="email" placeholder="请输入" v-model="email"></el-input>
                </el-form-item>-->
							</div>

							<!-- 右部分 -->
							<div class="form-right form-item fl">
								<el-form-item label="申请时间：">
									<p v-text='applyYear'></p>
								</el-form-item>
								<el-form-item label="承担单位：">
									<p v-text='applyYear'></p>
								</el-form-item>
							</div>
						</div>
					</div>
					<!-- 申报明细信息 -->
					<p class="headline" style="marginTop:20px">申报明细信息</p>
					<div class="clearfix detailInfo">
						<i class="iconfont icons-PDFtubiao fl"></i>
						<span style="marginRight:20px">人工智能项目申报申请书.PDF</span>
						<span class="blue" @click="preview()">预览</span>
						<span class="blue" @click="download()">下载</span>
					</div>

					<!-- 评审专家列表 -->
					<div class="clearfix">
						<p class="headline fl" style="marginTop:20px">评审专家列表</p>
						<el-button class="dt-btn fr" @click="chooseMaster()">抽取专家</el-button>
					</div>
					<!-- 表格 -->
					<el-table :data="expertList" tooltip-effect="dark" style="width: 100%" :header-cell-style="{background:'#EBEBEB'}"
					 class="drafts">
						<!-- <el-table-column type="selection" width="55"></el-table-column> -->
						<!-- show-overflow-tooltip控制超过单元格的内容是否用...隐藏 -->
						<el-table-column prop="expertNum" label="员工编号" show-overflow-tooltip width></el-table-column>
						<el-table-column prop="expertName" label="专家名称" show-overflow-tooltip width></el-table-column>
						<el-table-column prop="expertSex" label="性别" show-overflow-tooltip></el-table-column>
						<el-table-column prop="expertTitle" label="职称" show-overflow-tooltip width></el-table-column>
						<el-table-column prop="expertOrg" label="单位" show-overflow-tooltip width></el-table-column>
						<el-table-column prop="expertSubject" label="学科" show-overflow-tooltip width></el-table-column>
						<el-table-column prop="expertField" show-overflow-tooltip label="擅长领域" width></el-table-column>
						<el-table-column label="操作">
							<template slot-scope="scope">
								<!-- 编辑按钮 -->
								<!-- <el-button size="mini" type="primary" icon="el-icon-edit" @click="hao()" circle></el-button>  -->
								<!-- 申请按钮 -->
								<span class="dt-txt-btn" @click="handleDelete(scope.row)">删除</span>
							</template>
						</el-table-column>
					</el-table>
					<!-- 备注说明 -->
					<div>
						<p class="headline" style="marginTop:20px">备注说明</p>
						<el-form-item>
							<dt-textarea :value.sync="approvalComments" :maxNumber="2000"></dt-textarea>
						</el-form-item>
					</div>
					<!-- 底部 -->
					<div class="clearfix">
						<!-- 附件列表 -->
						<div class="fl listOuter">
							<p class="headline" style="marginTop:20px">附件列表</p>
							<div class="list">
								<ul>
									<li v-for="attachment in attachmentList" :key="attachment.id" class="clearfix">
										<span class="previewLeft">{{attachment.attachmentName}}</span>
										<span class="blue previewRight">预览</span>
									</li>
								</ul>
							</div>
						</div>

						<!-- 审批历史 -->
						<div class="fl historyOuter">
							<p class="headline" style="marginTop:20px">审批历史</p>
							<div class="history">
								<!-- 表格 -->
								<el-table :data="approvalCommentList" tooltip-effect="dark" style="width: 100%" :header-cell-style="{background:'#EBEBEB'}"
								 class="drafts">
									<!-- <el-table-column type="selection" width="55"></el-table-column> -->
									<!-- show-overflow-tooltip控制超过单元格的内容是否用...隐藏 -->
									<el-table-column prop="approver" label="审批人" show-overflow-tooltip width="100"></el-table-column>
									<el-table-column prop="approvalTime" label="审批时间" show-overflow-tooltip width></el-table-column>
									<el-table-column prop="approvalComments" label="意见" show-overflow-tooltip></el-table-column>
								</el-table>
							</div>
						</div>
					</div>
				</div>
			</el-form>
		        <transition> 
         <el-dialog title="组建评审专家小组" :visible.sync="dialogShows" width="70%"  height="80%" :append-to-body="true" >
            <TodoShows height="100%"></TodoShows> 
        </el-dialog > 
      </transition>  
    </div> 
	</dt-scroll-box>
</template>
<script>
	import TodoShows from './TodoList'
	/*
	 *项目申报-项目申报入口界面
	 * @author renchao 2019-02-26
	 */
	export default {
		data() {
			return {
				// 备注说明
				textarea: "",
				applyYear: "",
				centerDialogVisible: false,
				applyNum: "",
				dialogShows:false,
				applyDate: "",
				applyOrg: "",
				approvalComments: "",
				projectId: 0,
				attachmentList: [],
				approvalCommentList: [],
				expertList: []
			};
		},
		created() {

			this.projectId = this.$route.query.orderId;
			this.$dt.getAjax(
				'/project/approveSrpmsProject/' + this.projectId, {},
				(res) => {

					//this.tableData = res;
					this.applyNum = res.applyNum;
					this.expertList = res.expertList;
					this.applyYear = res.applyYear;
					this.attachmentList = res.attachmentList;
					this.approvalCommentList = res.approvalCommentList;
				},
				() => {

				}
			)
		},
		  components: { 
        TodoShows
    },
		methods: {
			//   同意按钮
			agree() {
				this.$dt.postAjax(
					'/project/approveSrpmsProject/' + this.projectId, {
						"approvalComments": this.approvalComments
					},
					(res) => {
								this.$router.push("/myBacklog");
					},
					() => {

					}
				)
				// 拒绝按钮
			},
			refuse() {},
			//  处理删除
			handleDelete() {},
			//抽取专家
			chooseMaster() {
				this.dialogShows=true
				// this.$router.push('/')
			},
			// 预览
			preview() {},
			//下载
			download() {}
		}
	};
</script>
<style lang="less">
	.proApplyCheck-page {
		padding-bottom: 60px;
		margin-top: 30px;

		.mainContent {
			padding-bottom: 400px;
		}

		.txt {
			font-size: 18px;
			font-weight: 700;
		}

		.title {
			font-size: 26px;
			color: #000000;
			letter-spacing: -0.42px;
			text-align: center;
			margin-bottom: 30px;
			font-weight: 700;
		}

		//   公共小标题样式
		.headline {
			font-size: 18px;
			color: #63b4ff;
			padding-left: 10px;

			margin-bottom: 20px;
			border-left: 4px solid #63b4ff;
			height: 18px;
			line-height: 18px;
		}

		//   @media screen and(max-width: 1600px) {
		//     .el-table {
		//       font-size: 14px !important;
		//     }
		//     .drafts {
		//       .dt-txt-btn {
		//         font-size: 12px;
		//         margin-left: 0 !important;
		//       }
		//     }
		//   }

		//   .drafts .el-table__row td:nth-child(n + 7) {
		//     color: #63b4ff;
		//   }
		.blue {
			color: #63b4ff;
			margin-left: 14px;
		}

		.blue:hover {
			cursor: pointer;
			color: #63b4ff;
			font-weight: 700;
		}

		.icons-PDFtubiao {
			font-size: 30px;
			margin-right: 14px;
			color: #ea4318;
		}

		.detailInfo {
			height: 50px;
			line-height: 50px;
			font-size: 16px;
		}

		.selection {

			//小屏
			@media screen and(max-width: 1599px) {
				.el-form-item__label {
					font-size: 14px;
					width: 92px;
				}

				.el-input {
					font-size: 14px;
					max-width: 200px;
				}

				.el-date-editor.time {
					max-width: 200px;
				}
			}

			//大屏
			@media screen and(min-width:1600px) {
				.el-form-item__label {
					font-size: 16px;
					width: 92px;
				}

				.el-input {
					font-size: 16px;
					max-width: 222px;
				}

				.el-date-editor.time {
					max-width: 222px;
				}
			}

			// .el-input {
			//   font-size: 16px;
			//   max-width: 222px;
			// }
			margin-top: 20px;

			.form-item {}

			.el-input--prefix .el-input__inner {
				padding-left: 15px;
				//   box-sizing: border-box;
			}

			.el-date-editor.el-input {
				width: 100%;
			}

			.name {
				.el-input__inner {
					max-width: 222px;
				}
			}

			//   小屏
			@media screen and (max-width: 1600px) {
				.form-item {
					width: 33%;
					margin-left: 0;
				}

				//   .form-right .el-form-item__label {
				//     width: 90px !important;
				//   }
				.el-input__prefix {
					left: 86%;
				}
			}

			//大屏
			@media screen and (min-width: 1601px) {
				.form-item {
					width: 26%;
					margin-left: 5%;
				}

				//   .form-right .el-form-item__label {
				//     width: 100px !important;
				//   }
				.el-input__prefix {
					left: 86%;
				}
			}
		}

		//   表格
		.el-table {

			//小屏
			@media screen and(max-width: 1600px) {
				font-size: 14px;
			}

			//大屏
			@media screen and(min-width:1601px) {
				font-size: 16px;
			}

			border: 1px solid #ebebeb;

			thead {
				color: #696969;
				font-weight: 700;
			}

			.cell {
				text-align: center;
			}
		}

		.listOuter {
			margin-right: 1.6%;

			//小屏
			@media screen and(max-width: 1600px) {
				width: 35.4%;
			}

			//大屏
			@media screen and(min-width:1601px) {
				width: 24.4%;
			}
		}

		.list {
			border: 1px solid #ccc;

			//小屏
			@media screen and(max-width: 1600px) {
				font-size: 14px;
			}

			//大屏
			@media screen and(min-width:1601px) {
				font-size: 16px;
			}

			li {
				height: 48px;
				line-height: 48px;
				border-bottom: 1px solid #ccc;
			}

			li:last-child {
				border: none;
			}

			.previewRight {
				float: right;
				margin-right: 15px;
				text-align: center;
			}

			.previewLeft {
				float: left;
				padding-left: 10px;
			}
		}

		.historyOuter {
			@media screen and(max-width: 1600px) {
				width: 63%;
				font-size: 14px;
			}

			//大屏
			@media screen and(min-width:1601px) {
				width: 74%;
			}
		}
	}
</style>



